@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800&display=swap');
@mixin fcc{
    display: flex;
    align-items: center;
    justify-content: center;
}
@mixin fc{
    display: flex;
    align-items: center;
}
@mixin font($f-s,$f-w){
    font-family: "Poppins", sans-serif;
    font-size: $f-s;
    font-weight: $f-w;
}